/* Popup style */
.popup-box {
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.box {
  position: relative;
  width: 600px;
  margin: 0 auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh - 20px);
  background: #fff;
  border-radius: 16px;
  padding: 20px;
  padding-left: 30px;
  border: 1px solid #999;
  overflow: auto;
}

.close-icon {
  content: "x";
  cursor: pointer;
  /* position: fixed;
    right: calc(15% - 30px);
    top: calc(100vh - 85vh - 33px); */
  position: absolute;
  left: 600px;
  top: 10px;
  background: white;
  width: 25px;
  height: 20px;
  border-radius: 50%;
  line-height: 15px;
  text-align: center;
  /* border: 1px solid #999; */
  font-size: 20px;
  color: grey;
}

#popup-btn {
  display: flex;
  flex-direction: row;
  margin: auto;
  margin-bottom: 16px;
  align-items: center;
  padding: 12px 30px;
  width: 530px;
  height: 68px;
  background: #f8f9fa;
  border: 1px solid #cfd8dc;
  border-radius: 12px;
}

